.skillset-area{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20rem;
 }
 
 .skillset-area h1{
   color: white;
   transition: 700ms ease;
   font-variation-settings: "wght";
 }
 
 .skillset-area h1:hover{
     font-variation-settings: "wght" 582; 
     letter-spacing: 2px;
 }
 
 
 .skillset-box {
  width: 80%;
  background-color: rgba(255, 255, 255, 0.5);;
  padding: 2rem;
  height: fit-content;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: top;
  justify-content: center;
  flex-wrap: wrap;
 }
 
 .skillset-box p{
  margin-left: 0;
  margin-right: 1rem;
  width: 60%;
  height: auto;
 }
 
 
 
 @media only screen and (max-width: 768px) {
   .skillset-area{
     margin-bottom: 10rem;
   }
  .skillset-box {
   width: 80%;
   background-color: rgba(255, 255, 255, 0.5);;
   padding: 2rem;
   height: 30rem;
   margin: 0 auto;
   display: flex;
   flex-direction: row;
   align-items: top;
   justify-content: center;
   flex-wrap: wrap;
   overflow-y: scroll; /* enable scrolling */
  }
  
  .skillset-box img {
    width: 80%;
    order: 1;
    margin-bottom: 1rem;
  }
  
  .skillset-box p {
    width: 100%;
    margin-left: 0;
    margin-bottom: 3rem;
  }
 }
 