.skill-box{
    margin: 3rem 3rem;
    width: 15rem;
    height: auto;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.skill-box-image{
    height: 20%;
    width: 70%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: auto;
}

.skill-box-text{
    word-wrap: normal;
    text-align: center;
    padding: 1rem;
}

.skill-box-image img{
    width: 100%;
    height: auto;
}

@media (max-width: 1000px) {
    .skill-box{
        width: 10rem;
        height: auto;
        margin: 2rem 1rem;
    }
}

@media (max-width: 768px) {
    .skill-box{
        width: 8rem;
        height: auto;
        margin: 1rem 1rem;
    }

    .skill-box-text{
        font-size: 12px;
    }
}

@media (max-width: 500px) {
    .skill-box{
        width: 6rem;
        height: auto;
        margin: 1rem 0.5rem;
    }

    .skill-box-text{
        font-size: 10px;
    }
}
