.navbar {
  background: rgba(255, 255, 255, 0.9);
  position: relative;
  height: 4rem;
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30rem;
  margin-left: 5%;
  margin-right: 5%;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 0 auto;
}

.navbar-menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}

.navbar-item {
  margin-right: 5rem;
  transition: transform 0.8s ease-in-out;
}

.navbar-item:hover {
  background-color: white;
  transform: scale(1.1);
}

.navbar-link {
  font-family: 'Source Code Pro', monospace;
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 40px;
  text-align: center;
  letter-spacing: 0.05em;
  text-decoration: none;
  color: #000000;
  transition: color 0.2s ease-in-out;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

.navbar-link:hover {
}

@media (max-width: 1100px) {
  .navbar {
    position: static;
    margin-top: 10rem;
    height: 10rem; 
    margin-bottom: 8rem;
  }
  .navbar-menu{
    display: flex;
    height: 100%;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 1rem;
  }

  .navbar-item{
    margin-right: 2rem;
    margin-bottom: 1rem;
  }

  .navbar-item:hover {
    background-color: transparent;
    transform: scale(1.1);
  }

  .navbar-link{
    background-color:white;
  }
}

@media (max-width: 600px) {
  .navbar {
    position: static;
    margin-top: 5rem;
    height: 15rem; 
    margin-bottom: 8rem;
  }
}